.read-more-wrap.ijf-databases-block {
    width: 100%;
    padding: 4vmin 4vmin 0 4vmin ;
    margin: 0 auto;
    background: var(---colour-bg);
}

.ijf-databases-block .post-card {
    background-color: var(--colour-white);
    min-height: 100px;
    margin: 0;
    padding: 0;    
}

.ijf-databases-block .post-card .post-card-title {
    color: var(--colour-black);
    text-align: left;
    width: 100%;
    margin: 0;
    padding: 0;
    font-size: 1.5em;
}